import React from "react";
import Header from "./Header";
import Generator from "./Generator";
import Preview from "./Preview";
import { HEAD, END } from "./ResponsiveTemplateStaticBits";
import "./App.css";

import {
  Notification,
  Delete,
  Modal,
  ModalBackground,
  ModalCard,
  ModalCardHeader,
  ModalCardTitle,
  ModalCardBody
} from "bloomer";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showDeleteNotification: false,
      showSuccessNotification: false,
      showDebugNotification: false,
      showHelpModal: false,
      thisWeek: true,
      emailTitle: "Events This Week",
      emailSubtitle: "Come out this week to get involved!",
      eventCards: [
        {
          title: "",
          location: "",
          description: "",
          date: "",
          startTime: "",
          endTime: "",
          imageURL: "",
          facebookURL: ""
        }
      ]
    };
  }

  incrementEventCards = () => {
    let tempCards = [...this.state.eventCards];
    tempCards.push({
      title: "",
      location: "",
      description: "",
      date: "",
      startTime: "",
      endTime: "",
      imageURL: "",
      facebookURL: ""
    });
    this.setState({ eventCards: tempCards });
  };

  decrementEventCards = index => {
    if (window.confirm("Are you sure you want to delete this event?")) {
      let tempCards = [...this.state.eventCards];
      tempCards.splice(index, 1);
      setTimeout(this.removeDeleteNotification, 3000);
      this.setState({ eventCards: tempCards, showDeleteNotification: true });
    }
  };

  removeDeleteNotification = () => {
    this.setState({ showDeleteNotification: false });
  };

  showSuccessNotification = () => {
    setTimeout(this.removeSuccessNotification, 3000);
    this.setState({ showSuccessNotification: true });
  };

  removeSuccessNotification = () => {
    this.setState({ showSuccessNotification: false });
  };

  showDebugNotification = () => {
    setTimeout(this.removeDebugNotification, 6000);
    this.setState({ showDebugNotification: true });
  };

  removeDebugNotification = () => {
    this.setState({ showDebugNotification: false });
  };

  openHelpModal = () => {
    this.setState({ showHelpModal: true });
  };

  hideHelpModal = () => {
    this.setState({ showHelpModal: false });
  };

  copyToClipboard = () => {
    let body = document.getElementById("template-container").innerHTML;

    navigator.clipboard.writeText(String(HEAD + body + END));

    this.showSuccessNotification();
  };

  debug = () => {
    navigator.clipboard.writeText(JSON.stringify(this.state));

    this.showDebugNotification();
  };

  toggleThisWeek = () => {
    this.setState({ thisWeek: !this.state.thisWeek });
  };

  updateEventCardValue = (index, field, value) => {
    console.log("index: ", index, "field: ", field, "value: ", value);
    let tempCards = [...this.state.eventCards];
    tempCards[index][field] = value;
    this.setState({ eventCards: tempCards });
  };

  updateTitleValue = (field, value) => {
    console.log("field: ", field, "value: ", value);
    let tempState = { ...this.state };
    tempState[field] = value;
    this.setState(tempState);
  };

  render() {
    return (
      <div className="App">
        <link
          rel="stylesheet"
          href="https://cdn.jsdelivr.net/npm/bulma@0.8.0/css/bulma.min.css"
        ></link>
        <script src="https://unpkg.com/computed-style-to-inline-style"></script>

        <Header
          copyToClipboard={this.copyToClipboard}
          debug={this.debug}
          openHelpModal={this.openHelpModal}
        />
        <div id="content">
          <Generator
            eventCards={this.state.eventCards}
            incrementEventCards={this.incrementEventCards}
            decrementEventCards={this.decrementEventCards}
            updateEventCardValue={this.updateEventCardValue}
            thisWeek={this.state.thisWeek}
            toggleThisWeek={this.toggleThisWeek}
            emailTitle={this.state.emailTitle}
            emailSubtitle={this.state.emailSubtitle}
            updateTitleValue={this.updateTitleValue}
          />
          <Preview
            eventCards={this.state.eventCards}
            thisWeek={this.state.thisWeek}
            emailTitle={this.state.emailTitle}
            emailSubtitle={this.state.emailSubtitle}
          />
          {this.state.showDeleteNotification ? (
            <Notification isColor="danger" id="delete-notification">
              <Delete onClick={this.removeDeleteNotification} />
              Event Deleted
            </Notification>
          ) : (
            ""
          )}
          {this.state.showSuccessNotification ? (
            <Notification isColor="success" id="success-notification">
              <Delete onClick={this.removeSuccessNotification} />
              Copied to Clipboard
            </Notification>
          ) : (
            ""
          )}
          {this.state.showDebugNotification ? (
            <Notification isColor="success" id="success-notification">
              <Delete onClick={this.removeDebugNotification} />
              Debug info copied to clipboard
              <br />
              Take a screenshot and send both to the person responsible for the
              site!
            </Notification>
          ) : (
            ""
          )}
          <Modal isActive={this.state.showHelpModal}>
            <ModalBackground />
            <ModalCard>
              <ModalCardHeader>
                <ModalCardTitle>How To Use This Website</ModalCardTitle>
                <Delete onClick={this.hideHelpModal} />
              </ModalCardHeader>
              <ModalCardBody>
                <ol>
                  <li>Add all the event details</li>
                  <li>Make sure everything is correct</li>
                  <li>Click Copy in the top right corner of the site</li>
                  <li>Paste into any text editor like notepad or word</li>
                  <li>Save as anyname.html</li>
                  <li>
                    Open anyname.html in any browser by right clicking the file
                    and selecting Open With, or by opening the browser and
                    pressing ctrl + o, then navigating to anyname.html
                  </li>
                  <li>
                    Press ctrl + a to select the whole page, then ctrl + c to
                    copy it, then paste it into the compose window of any email
                    site
                  </li>
                  <li>Send the email!</li>
                </ol>
                <hr />
                <b>Weird Quirks</b>
                <p>
                  The email will look better once you've sent it than it will in
                  the preview.
                </p>
                <p>
                  The time input has three fields: hour, minute, and AM/PM. To
                  select AM or PM, type AM or PM in that field.
                </p>
                <p>It's a work in progress. Cut a comrade some slack.</p>
              </ModalCardBody>
            </ModalCard>
          </Modal>
        </div>
      </div>
    );
  }
}
export default App;
