import React from "react";

import { Title, Button } from "bloomer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes, faCheck } from "@fortawesome/free-solid-svg-icons";

import EventCard from "./EventCard";
import TitleEditorCard from "./TitleEditorCard";

function Generator(props) {
  return (
    <section id="Generator">
      <nav className="sectionNav">
        <div>
          <Button isColor="danger" isOutlined onClick={props.toggleThisWeek}>
            <span>This Week?</span>
            <span className="icon">
              <FontAwesomeIcon icon={props.thisWeek ? faCheck : faTimes} />
            </span>
          </Button>
        </div>
        <Title isSize={2}>Events</Title>
        <div>
          <Button
            id="add-button"
            isColor="danger"
            isOutlined
            onClick={props.incrementEventCards}
          >
            Add Event
          </Button>
        </div>
      </nav>
      <div id="event-container">
        {props.thisWeek ? (
          <TitleEditorCard
            emailTitle={props.emailTitle}
            emailSubtitle={props.emailSubtitle}
            updateTitleValue={props.updateTitleValue}
          />
        ) : (
          ""
        )}

        {props.eventCards.map((eventCard, index) => (
          <EventCard
            title={eventCard.title}
            description={eventCard.description}
            date={eventCard.date}
            imageURL={eventCard.imageURL}
            facebookURL={eventCard.facebookURL}
            updateValue={props.updateEventCardValue}
            deleteMe={props.decrementEventCards}
            index={index}
            key={index}
          />
        ))}
      </div>
    </section>
  );
}

export default Generator;
