import React from "react";

import { Box, Label, Field, Control, Input } from "bloomer";

function TitleEditorCard(props) {
  return (
    <Box className="EventCard">
      <Field>
        <Label>Email Title</Label>
        <Control>
          <Input
            type="text"
            value={props.emailTitle}
            onChange={e => props.updateTitleValue("emailTitle", e.target.value)}
          />
        </Control>
      </Field>
      <Field>
        <Label>Subtitle</Label>
        <Control>
          <Input
            type="text"
            value={props.emailSubtitle}
            onChange={e =>
              props.updateTitleValue("emailSubtitle", e.target.value)
            }
          />
        </Control>
      </Field>
    </Box>
  );
}

export default TitleEditorCard;
